<template>
  <v-card>
    <v-card-title>

    </v-card-title>
    <v-card-text>
      <v-row>
        <!-- <v-col cols="12">
                <v-text-field :label="$t('label_The_parameter_name')+'*'"
                              :error-messages="$errors('list','param_name',requiredRule)"
                              :readonly="disabled"
                              v-model="list.param_name">
                </v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field :label="$t('label_describe')"
                              :readonly="disabled"
                              v-model="list.param_description">
                </v-text-field>
              </v-col> -->
        <v-col cols="12">
          <mrc_ess_multiText :label="$t('label_The_parameter_name')+'*'"
                             :isNational="judegNational('param_name')"
                             :error-messages="$errors('list','param_name',requiredRule)"
                             :readonly="disabled"
                             v-model="list.param_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <mrc_ess_multiText :label="$t('label_describe')"
                             :isNational="judegNational('param_description')"
                             :readonly="disabled"
                             v-model="list.param_description">
          </mrc_ess_multiText>
        </v-col>
        <!-- <v-col cols="12">
                <mrc_ess_prompt v-model="list.client_type"
                                :label="$t('label.client_type') + '*'"
                                :disabled='disabled'
                                :rules="false"
                                :errMsg="$errors('list','client_type',requiredRule)"
                                multiple
                                obj='client_type'></mrc_ess_prompt>
              </v-col> -->
        <!-- <v-col cols="12">
                <v-text-field :label="$t('label_Parameters_of_the_key_name')+'*'"
                              :readonly="disabled"
                              :error-messages="$errors('list','param_key',requiredRule)"
                              v-model="list.param_key"
                              >
                </v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="list.param_key_value"
                              :readonly="disabled"
                              :error-messages="$errors('list','param_key_value',requiredRule)"
                              :label="$t('label_value')+'*'">
                </v-text-field>
              </v-col> -->
        <v-col cols="12">
          <mrc_ess_multiText :label="$t('label_Parameters_of_the_key_name')+'*'"
                             :readonly="disabled"
                             :isNational="judegNational('param_key')"
                             :error-messages="$errors('list','param_key',requiredRule)"
                             v-model="list.param_key">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <mrc_ess_multiText v-model="list.param_key_value"
                             :readonly="disabled"
                             :isNational="judegNational('param_key_value')"
                             :error-messages="$errors('list','param_key_value',requiredRule)"
                             :label="$t('label_value')+'*'">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <mrc_ess_prompt label="Business_type*"
                          :disabled='disabled'
                          :rules='false'
                          :chips="false"
                          :errMsg="$errors('list','business_type',requiredRule)"
                          v-model="list.business_type"
                          obj="business_type">
          </mrc_ess_prompt>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @cancel='cancel'
                       @success='success'></mrc_ess_submitBtn>
  </v-card>
</template>
<style scoped>
</style> 
<script>  
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { getSystemParameterList, detailSystemParameter, addSystemParameter, updateSystemParameter, deleteSystemParameter } from '@/service/api/systemParameter.js'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      items: [],
      list: {},
    };
  },
  validations: {
    list: {
      param_key: {
        required
      },
      param_key_value: {
        required
      },
      param_name: {
        required
      },
      business_type: {
        required
      },
      param_description: {

      }
    }
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_params_setting')
    if (this.$store.state.uuid) {
      await this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },
  methods: {
    async getDetail () {
      const res = await detailSystemParameter({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = this.$deletefield(null, res.data)
      }
      else {

      }
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑嘛?'
      }).then(res => {
        this.$router.push({
          name: 'systemParameter'
        })
      }).catch(err => { })
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      if (this.$store.state.uuid) {
        updateSystemParameter(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
        })
      }
      else {
        addSystemParameter(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'systemParameter' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
        })
      }

    }
  },
  computed: {

  },
  watch: {},
};
</script>

