<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>
    <systemParameter :disabled='readonly'></systemParameter>
  </div>
</template>

<script>
import {
  getSystemParameterList, detailSystemParameter
  , addSystemParameter, updateSystemParameter, deleteSystemParameter
} from '@/service/api/systemParameter.js'
import systemParameter from './modules/systemParameter'
export default {
  components: {
    systemParameter
  },
  props: {},
  data () {
    return {
      readonly: false,
      tab: null,
      items: [
        { tab: '定时任务设定' },
        { tab: '日志' },
      ],
      list: {}
    };
  },
  computed: {},
  created () { },
  watch: {},
  mounted () {
    this.readonly = this.$store.state.iconstatus
    this.$route.meta.title = this.$store.getters.subPage_name
    this.tab = this.$store.state.tab
  },
  beforeDestroy () {
    this.$store.commit('setTab', '')
  },

  methods: {
    change () {
      this.$store.commit('setTab', this.tab)
    },
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
        deleteSystemParameter({ uuid: this.$store.state.uuid }).then(res => {
          if (res.code === '200') {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.push({
              name: 'systemParameter'
            })
          }
        })
      })
    },
  },
};
</script>

